import firebase from "firebase/compat/app"
import "firebase/compat/firestore"
import { ref } from "@vue/reactivity"
import { onUnmounted } from "@vue/runtime-dom"
import { enableIndexedDbPersistence } from "firebase/firestore"; 


const config = {
    apiKey: "AIzaSyC7lEUWH9z2H2QRezoQJvu2AZ5e5ev6WLM",
    authDomain: "julix-b1777.firebaseapp.com",
    projectId: "julix-b1777",
    storageBucket: "julix-b1777.appspot.com",
    messagingSenderId: "875585337078",
    appId: "1:875585337078:web:ef49a54cc9e99f3360ded7",
    measurementId: "G-1TRS9LPWKQ"
}

const firebaseapp = firebase.initializeApp(config)

const db = firebaseapp.firestore()
const itemsColeccion = db.collection("items")
const pedidosColeccion = db.collection("pedidos")
const clientesColeccion = db.collection("clientes")


enableIndexedDbPersistence(db)
  .catch((err) => {
      if (err.code == 'failed-precondition') {
          // Multiple tabs open, persistence can only be enabled
          // in one tab at a a time.
          // ...
      } else if (err.code == 'unimplemented') {
          // The current browser does not support all of the
          // features required to enable persistence
          // ...
      }
  });


export const incrementa = firebase.firestore.FieldValue.increment();

export const createItem = item => {
    return itemsColeccion.add(item)
}

export const createPedido = pedido => {
    return pedidosColeccion.add(pedido)
}

export const createCliente = cliente => {
    return clientesColeccion.add(cliente)
}

export const getItem = async id => {
    const item = await itemsColeccion.doc(id).get()
    return item.exists ? item.data() : null
}

export const getPedido = async id => {
    const pedido = await pedidosColeccion.doc(id).get()
    return pedido.exists ? pedido.data() : null
}

export const getCliente = async id => {
    const cliente = await clientesColeccion.doc(id).get()
    return cliente.exists ? cliente.data() : null
}

export const updateItem = (id, item) => {
    return itemsColeccion.doc(id).update(item)
}

export const updateCliente = (id, item) => {
    return clientesColeccion.doc(id).update(item)
}

export const deleteItem = id => {
    return itemsColeccion.doc(id).delete()
}

export const useLoadItems = () => {
    const items = ref([])
    const close = itemsColeccion.onSnapshot(snapshot => {
        items.value = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }))
    })
    onUnmounted(close)
    return items
}

export const useLoadPedidos = () => {
    const pedidos = ref([])
    const close = pedidosColeccion.onSnapshot(snapshot => {
        pedidos.value = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }))
    })
    onUnmounted(close)
    return pedidos
}

export const useLoadClientes = () => {
    const clientes = ref([])
    const close = clientesColeccion.onSnapshot(snapshot => {
        clientes.value = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }))
    })
    onUnmounted(close)
    return clientes
}